.home {
  height: 100vh;
  background: $cherry-gradient;
}

.home__top {
  @extend .centered;
  padding: $separator-xxl;
}

.home__cherry {
  margin-top: $separator-xxxl;
}

.home__title {
  @extend .title-h1;

  color: $color-black;
  margin-top: $separator-xxxl;
  text-align: center;
}

.home__options-wrapper {
  width: 100%;
  background-color: $color-white;
  position: fixed;
  bottom: 0;
  padding: $separator-m;

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 8px;
}

.home__host-button {
  text-align: right;
}

.home__guest-button, 
.home__host-button {
  .button {
    width: 100%;
    max-width: 400px;
    .button__content {
      font-size: 20px;
    }
  }
}

@media #{$breakpoints-large} {
  .home__top {
    padding-top: $separator-xxxxl;
  }

  .home__header {
    display: flex;
  }

  .home__cherry {
    margin-left: $separator-xxxl;
  }

  .home__options-wrapper {
    padding: $separator-xl;
  }

  .home__guest-button,
  .home__host-button {
  .button {
    .button__content {
      font-size: 32px;
    }}
  }
}
