@import '../../../../style/colors';

.host-answers__wrapper {
  //@extend .grid;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: calc(100vh - #{$header-back-height});
}
.host-answers__title {
  @extend .title-h1;

  margin-top: $separator-xxxl;
}

.host-answers__columns {
  width: 100%;
  display: flex;
}

.host-answers__image {
  display: flex;
  justify-content: center;
  width: 100%;
  img {
    max-width: 100%;
    max-height: 75vh;
  }
}

.host-answers__bars {
  //display: flex;

  width: 100%;
  height: 75vh;
  padding: $separator-xxl;
}

.bar-stats__bar:nth-child(1) {
  .bar-stats__bar-progress,
  .bar-stats__bar-fixed {
    background-color: $color-response-01;
  }
}

.bar-stats__bar:nth-child(2) {
  .bar-stats__bar-progress,
  .bar-stats__bar-fixed {
    background-color: $color-response-02;
  }
}

.bar-stats__bar:nth-child(3) {
  .bar-stats__bar-progress,
  .bar-stats__bar-fixed {
    background-color: $color-response-03;
  }
}

.bar-stats__bar:nth-child(4) {
  .bar-stats__bar-progress,
  .bar-stats__bar-fixed {
    background-color: $color-response-04;
  }
}
