$quiz-card-border-radius: 8px;

.quiz-card {
  @extend .hover-basic;

  display: flex;
  justify-content: center;
  background-color: $cherry-light;
  color: $color-white;
  padding: $gutter;
  border-radius: $quiz-card-border-radius;

  &:hover {
    background-color: $cherry-dark;
  }
}

.quiz-card__name {
  @extend .title-h2;
}
