$button-padding-horizontal: $separator-l;
$button-padding-vertical: $separator-m;

.button {
  @extend .hover-basic;
  @extend .button;

  font-family: 'Signika', sans-serif;
  padding: $button-padding-vertical $button-padding-horizontal;
  border-color: transparent;
  border-radius: $border-radius-01;
  color: $color-white;
}

.button--color-light {
  background-color: $cherry-light;
}
.button--color-dark {
  background-color: $cherry-dark;
}

.button--size-big {
  height: 70px;
  width: 200px;

  .button__content {
    font-size: 24px;
  }
}
.button--size-xl {
  height: 100px;

  .button__content {
    font-size: 32px;
  }
}
